import { User } from 'src/app/models/user';
import { AuthService } from 'src/app/services/auth.service';
import { SessionsManagerService } from 'src/app/services/sessions-manager-service';
import { Session, SessionType, SessionTimingType } from 'src/app/models/session';
import { SessionInfoComponent } from './../../common/session-info/session-info.component';
import { AlertService } from 'src/app/services/alert.service';
import { concatMap, takeUntil, take } from 'rxjs/operators';
import { ModalController } from '@ionic/angular';
import { ProviderService } from './../../services/provider.service';
import { Provider } from './../../models/provider';
import { ActivatedRoute } from '@angular/router';
import { CalendarService } from "./../../services/calendar.service";
import { Subscription, from, Subject } from "rxjs";
import { AvailableHours } from "./../../models/available-hours";
import { EnvService } from "src/app/services/env.service";
import { Component, OnInit } from "@angular/core";
import { DatePipe } from "@angular/common";
import { SharedResourceAsyncRequestBundle } from 'src/app/architecture/shared-resource-async-request-bundle';

@Component({
  selector: "app-usr-view-provider-calendar",
  templateUrl: "./usr-view-provider-calendar.page.html",
  styleUrls: ["./usr-view-provider-calendar.page.scss"],
})
export class UsrViewProviderCalendarPage implements OnInit {
  segments = [];
  selectedSegmentValue:Date;
  unfilteredAvailableHoursArray: AvailableHours[] = [];
  calendarSubscription: Subscription;
  filteredHours: AvailableHours[] = [];
  mySessionsShared;
  providerSessionsShared:SharedResourceAsyncRequestBundle<Session[],number >;
  providerId = null;
  provider:Provider = null;
  ngUnsubscribe = new Subject();
  minDate = null;
  maxDate =null;
  getReservationStateText = AvailableHours.getReservationStatusText;
  constructor(
    private env: EnvService,
    private calendarService: CalendarService,
    private route:ActivatedRoute,
    private providerService:ProviderService,
    private modalController:ModalController,
    private alert:AlertService,
    private sessionsManager:SessionsManagerService,
    private auth:AuthService
  ) {
    this.mySessionsShared = this.sessionsManager.myCurrentSessionsShared;
    this.providerSessionsShared = this.sessionsManager.selectedProviderReservedSessionsShared;
    
    this.selectedSegmentValue = new Date();
    this.selectedSegmentValue.setHours(0,0,0);
    this.minDate = new Date();
    this.maxDate = new Date();
    this.maxDate.setDate(this.maxDate.getDate() + 30);
  }
  
  ngOnInit() {
     this.mySessionsShared.request();
     
  }
  ionViewWillEnter() {
    
    this.providerId = this.route.snapshot.params.providerId;
    this.providerService.getById(this.providerId).pipe(takeUntil(this.ngUnsubscribe)).subscribe(provider=>{
      this.provider = provider;
      this.providerSessionsShared.request(this.provider.id);
    })
    //this.generateSegments();
    this.calendarSubscription = this.calendarService.availableHoursUpdated.pipe(takeUntil(this.ngUnsubscribe)).subscribe(
      (result) => {
        this.unfilteredAvailableHoursArray = result;
        this.fitlerHours();
      }
    );
    this.calendarService.getAvailableHours(this.providerId).subscribe();
  }
  
  fitlerHours() {
    this.filteredHours = this.unfilteredAvailableHoursArray.filter((hour) => {
      return hour.repeating_day_of_week ==this.selectedSegmentValue.getUTCDay()
    });
    const sessions = this.getDateSessions();
    this.filteredHours.forEach(hours=>{
      let foundOverlap = false;
      for(let i = 0; i < sessions.length;i++)
      {
        let sessionHours = new AvailableHours();
        sessionHours.time_from = this.env.dateStringToJalaliTime(sessions[i].reserved_from);
        sessionHours.time_to = this.env.dateStringToJalaliTime(sessions[i].reserved_to)
        if(this.calendarService.haveOverlap(hours, sessionHours))
        {
          hours.reserved = true;
          if(sessions[i].accepted)
          {
            hours.reserveAccepted = true;
          }
          this.auth.getUser().then(result=>{
           if( (result as User).id == sessions[i].user_id)
           {
             hours.reservedByMe = true;
           }
           AvailableHours.fillCSSClass(hours);
          });
          hours.overlappingSession = sessions[i];
          foundOverlap = true;
          break;

        }
      }
      
      if(!foundOverlap)
      {
        hours.reserved = false;
        hours.reserveAccepted = false;
        hours.reservedByMe = false;
        hours.cssClass = "";
        hours.overlappingSession = null;
      }
    })
  }
  getDateSessions()
  {
    let sessions = this.providerSessionsShared.resource().concat(this.mySessionsShared.resource());
  
    sessions=  sessions.filter(session=>{
      const selectedDate = new Date(this.selectedSegmentValue);
      const sessionDate = new Date(session.reserved_from);
      return sessionDate.getMonth() == selectedDate.getMonth() 
        && sessionDate.getDate() == selectedDate.getDate()
    });
    return sessions;
  }
  ionViewWillLeave() {
    this.calendarSubscription.unsubscribe();
    this.calendarService.emptyCalendar();
    this.provider = null;
  }
  getDateText()
  {

    let monthName = this.env.dateStringToJalaliMonthName(this.selectedSegmentValue);
    
    return this.env.dateStringToJalaliFullDate(this.selectedSegmentValue);
  }
  generateSegments() {
    let date = new Date();

    for (let i = 0; i < 31; i++) {
      let tempDate = new Date();
      tempDate.setHours(0,0,0);
      tempDate.setDate(date.getDate() + i);
      if(i == 0)
      {
        this.minDate = tempDate;
      }
      if(i == 30)
      {
        this.maxDate = tempDate;
      }
      let text = this.env.dateStringToJalaliDate(tempDate.toString(), "jMM/jDD");
      let value = tempDate.toString();
      this.segments.push({
        text: text,
        value: value,
      });
    }
  }
  segmentChanged(ev) {
    
    //this.selectedSegmentValue = ev.target.value;
    //this.fitlerHours();
  }
  refresh(ev) {
    this.mySessionsShared.request();
    this.calendarService.getAvailableHours(this.providerId).pipe(take(1)).subscribe(result=>{
      ev.target.complete();
    });
    
  }
 
  initiateReserve(hours:AvailableHours)
  {
    if(hours.reserved && hours.reservedByMe)
    {
      const props = {
        'session' : hours.overlappingSession,
        'providerPerspective':false
        };
      this.alert.presentModal(SessionInfoComponent, props).pipe(take(1)).subscribe();
    }
    if(!hours.reserved)
    {
      this.sessionsManager.reserveSession(this.selectedSegmentValue, hours,this.provider, SessionTimingType.RESERVATION,SessionInfoComponent);
      //this.alert.openSessionTypesActionSheet(this.openRequestModal, this, hours);

    }
  }
  
  
  defaultBackButtonHref()
  {
    return 'provider/'+ this.providerId;
  }
  ngOnDestroy(): void {
   this.ngUnsubscribe.next();
   this.ngUnsubscribe.complete();
    
  }
  dateSelected(ev)
  {
    
    let tempDate  = new Date(ev);
    this.selectedSegmentValue = tempDate;
    this.fitlerHours();
  }
}
